.artistesContainer {
    width: 50vw;
    height: 50vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.artistes {
    width: 90%;
    height: auto;
    object-fit: contain;
}

#qui {
    position: relative;
}

#planet1-qui {
    width: 10%;
    position: absolute;
    left: 5%;
    top: 5%;
    padding-left: 5%;
    z-index: -1;
}

@media screen and (max-width: 500px) {

    .artistesContainer {
        width: 75vw;
        height: 90vw;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}